import React, { useState } from 'react';
import { ArrowLeft, Search, Plus, Bookmark, ThumbsUp, MessageCircle, Eye, MoreHorizontal, X, List, Edit3, Users, TrendingUp } from 'lucide-react';
import CreateBoardPost from './CreateBoardPost';
import ClubDetail from './ClubDetail';
import './Board.css';

const Board = () => {
  const [currentView, setCurrentView] = useState('main'); // 'main', 'createPost', 'club', 'clubDetail'
  const [selectedBoard, setSelectedBoard] = useState(null);
  const [selectedClub, setSelectedClub] = useState(null);
  const [showWriteModal, setShowWriteModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState('latest');
  const [followedClubs, setFollowedClubs] = useState(['', '']);

  //  
  const boards = [
    { id: 1, name: '', description: '  ', icon: '', posts: 1234, members: 5678 },
    { id: 2, name: '', description: '   ', icon: '', posts: 567, members: 2345 },
    { id: 3, name: '', description: '   ', icon: '', posts: 890, members: 3456 },
    { id: 4, name: '', description: '  ', icon: '', posts: 456, members: 1789 },
    { id: 5, name: '', description: '  ', icon: '', posts: 234, members: 987, isClub: true },
    { id: 6, name: '', description: ' ', icon: '', posts: 678, members: 2134 }
  ];

  //  
  const clubs = [
    {
      id: 1,
      name: '',
      description: '   ',
      category: '',
      members: 156,
      posts: 89,
      image: 'https://images.unsplash.com/photo-1461749280684-dccba630e2f6?w=100&h=100&fit=crop&crop=center',
      isFollowed: true,
      tags: ['', 'IT', '']
    },
    {
      id: 2,
      name: '',
      description: '    ',
      category: '',
      members: 89,
      posts: 145,
      image: 'https://images.unsplash.com/photo-1481627834876-b7833e8f5570?w=100&h=100&fit=crop&crop=center',
      isFollowed: true,
      tags: ['', '', '']
    },
    {
      id: 3,
      name: '',
      description: '   ',
      category: '',
      members: 234,
      posts: 567,
      image: 'https://images.unsplash.com/photo-1516035069371-29a1b244cc32?w=100&h=100&fit=crop&crop=center',
      isFollowed: false,
      tags: ['', '', '']
    },
    {
      id: 4,
      name: '',
      description: '  ',
      category: '',
      members: 178,
      posts: 234,
      image: 'https://images.unsplash.com/photo-1493225457124-a3eb161ffa5f?w=100&h=100&fit=crop&crop=center',
      isFollowed: false,
      tags: ['', '', '']
    }
  ];

  //  
  const [posts, setPosts] = useState([
    {
      id: 1,
      title: '   !',
      content: '     .  ,      .',
      author: '',
      board: '',
      createdAt: '2024-01-15T10:30:00',
      likes: 23,
      comments: 8,
      views: 156,
      isLiked: false,
      isBookmarked: false,
      tags: ['', '', '']
    },
    {
      id: 2,
      title: '  ',
      content: 'MacBook Pro 13 2021 .   ,   .',
      author: '',
      board: '',
      createdAt: '2024-01-15T09:15:00',
      likes: 45,
      comments: 12,
      views: 289,
      isLiked: true,
      isBookmarked: true,
      tags: ['', '', '']
    },
    {
      id: 3,
      title: '  ',
      content: '    2  .     !',
      author: '',
      board: '',
      club: '',
      createdAt: '2024-01-15T08:45:00',
      likes: 67,
      comments: 15,
      views: 234,
      isLiked: false,
      isBookmarked: false,
      tags: ['', '']
    }
  ]);

  const handleBoardClick = (board) => {
    if (board.isClub) {
      setCurrentView('club');
    } else {
      setSelectedBoard(board);
      setCurrentView('board');
    }
  };

  const handleClubClick = (club) => {
    setSelectedClub(club);
    setCurrentView('clubDetail');
  };

  const handleFollow = (clubId) => {
    setFollowedClubs(prev => {
      const clubName = clubs.find(c => c.id === clubId)?.name;
      if (clubName) {
        return prev.includes(clubName) 
          ? prev.filter(name => name !== clubName)
          : [...prev, clubName];
      }
      return prev;
    });
  };

  const handleLike = (postId) => {
    setPosts(posts.map(post => 
      post.id === postId 
        ? { ...post, isLiked: !post.isLiked, likes: post.isLiked ? post.likes - 1 : post.likes + 1 }
        : post
    ));
  };

  const handleBookmark = (postId) => {
    setPosts(posts.map(post => 
      post.id === postId 
        ? { ...post, isBookmarked: !post.isBookmarked }
        : post
    ));
  };

  const filteredPosts = posts.filter(post => {
    if (selectedBoard && post.board !== selectedBoard.name) return false;
    if (selectedClub && post.club !== selectedClub.name) return false;
    if (searchQuery && !post.title.toLowerCase().includes(searchQuery.toLowerCase()) && 
        !post.content.toLowerCase().includes(searchQuery.toLowerCase())) return false;
    return true;
  });

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffMs = now - date;
    const diffMins = Math.floor(diffMs / 60000);
    const diffHours = Math.floor(diffMs / 3600000);
    const diffDays = Math.floor(diffMs / 86400000);

    if (diffMins < 60) return `${diffMins} `;
    if (diffHours < 24) return `${diffHours} `;
    return `${diffDays} `;
  };

  if (currentView === 'createPost') {
    return (
      <CreateBoardPost 
        onBack={() => setCurrentView('main')}
        board={selectedBoard}
        club={selectedClub}
      />
    );
  }

  //   
  if (currentView === 'club') {
    return (
      <div className="board-container">
        <div className="board-header">
          <div className="header-content">
            <button className="back-button" onClick={() => setCurrentView('main')}>
              <ArrowLeft size={20} />
            </button>
            <h1></h1>
            <div className="header-actions">
              <button className="search-toggle" onClick={() => setSearchQuery('')}>
                <Search size={20} />
              </button>
            </div>
          </div>
          
          <div className="search-container">
            <input
              type="text"
              placeholder=" ..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="search-input"
            />
          </div>
        </div>

        <div className="content">
          <div className="clubs-grid">
            {clubs.filter(club => 
              club.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
              club.description.toLowerCase().includes(searchQuery.toLowerCase())
            ).map(club => (
              <div key={club.id} className="club-card" onClick={() => handleClubClick(club)}>
                <div className="club-image">
                  <img src={club.image} alt={club.name} />
                </div>
                <div className="club-info">
                  <div className="club-header">
                    <h3 className="club-name">{club.name}</h3>
                    <button 
                      className={`follow-button ${club.isFollowed ? 'followed' : ''}`}
                      onClick={(e) => {
                        e.stopPropagation();
                        handleFollow(club.id);
                      }}
                    >
                      {followedClubs.includes(club.name) ? '' : ''}
                    </button>
                  </div>
                  <p className="club-description">{club.description}</p>
                  <div className="club-tags">
                    {club.tags.map(tag => (
                      <span key={tag} className="tag">#{tag}</span>
                    ))}
                  </div>
                  <div className="club-stats">
                    <span><Users size={14} /> {club.members}</span>
                    <span><Edit3 size={14} /> {club.posts}</span>
                    <span className="category">{club.category}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  //   
  if (currentView === 'clubDetail' && selectedClub) {
    return (
      <ClubDetail 
        club={selectedClub}
        onBack={() => setCurrentView('club')}
      />
    );
  }

  // Temporary backup of old clubDetail view
  if (currentView === 'oldClubDetail' && selectedClub) {
    return (
      <div className="board-container">
        <div className="board-header">
          <div className="header-content">
            <button className="back-button" onClick={() => setCurrentView('club')}>
              <ArrowLeft size={20} />
            </button>
            <h1>{selectedClub.name}</h1>
            <button className="write-button" onClick={() => setCurrentView('createPost')}>
              <Plus size={16} />
              
            </button>
          </div>
        </div>

        <div className="content">
          <div className="club-detail-header">
            <div className="club-detail-info">
              <img src={selectedClub.image} alt={selectedClub.name} className="club-detail-image" />
              <div className="club-detail-text">
                <h2>{selectedClub.name}</h2>
                <p>{selectedClub.description}</p>
                <div className="club-detail-stats">
                  <span><Users size={16} />  {selectedClub.members}</span>
                  <span><Edit3 size={16} />  {selectedClub.posts}</span>
                </div>
              </div>
            </div>
            <button 
              className={`follow-button large ${followedClubs.includes(selectedClub.name) ? 'followed' : ''}`}
              onClick={() => handleFollow(selectedClub.id)}
            >
              {followedClubs.includes(selectedClub.name) ? '' : ''}
            </button>
          </div>

          <div className="posts-section">
            <h3></h3>
            <div className="posts-list">
              {filteredPosts.map(post => (
                <div key={post.id} className="post-card">
                  <div className="post-header">
                    <div className="post-author">
                      <div className="author-avatar">{post.author.charAt(0)}</div>
                      <div className="author-info">
                        <span className="author-name">{post.author}</span>
                        <span className="post-time">{formatDate(post.createdAt)}</span>
                      </div>
                    </div>
                    <button className="more-button">
                      <MoreHorizontal size={16} />
                    </button>
                  </div>
                  
                  <div className="post-content">
                    <h4 className="post-title">{post.title}</h4>
                    <p className="post-text">{post.content}</p>
                    {post.tags && (
                      <div className="post-tags">
                        {post.tags.map(tag => (
                          <span key={tag} className="tag">#{tag}</span>
                        ))}
                      </div>
                    )}
                  </div>
                  
                  <div className="post-actions">
                    <button 
                      className={`action-button ${post.isLiked ? 'liked' : ''}`}
                      onClick={() => handleLike(post.id)}
                    >
                      <ThumbsUp size={16} />
                      <span>{post.likes}</span>
                    </button>
                    <button className="action-button">
                      <MessageCircle size={16} />
                      <span>{post.comments}</span>
                    </button>
                    <button className="action-button">
                      <Eye size={16} />
                      <span>{post.views}</span>
                    </button>
                    <button 
                      className={`action-button ${post.isBookmarked ? 'bookmarked' : ''}`}
                      onClick={() => handleBookmark(post.id)}
                    >
                      <Bookmark size={16} />
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }

  //   
  if (currentView === 'board' && selectedBoard) {
    return (
      <div className="board-container">
        <div className="board-header">
          <div className="header-content">
            <button className="back-button" onClick={() => setCurrentView('main')}>
              <ArrowLeft size={20} />
            </button>
            <h1>{selectedBoard.name}</h1>
            <button className="write-button" onClick={() => setCurrentView('createPost')}>
              <Plus size={16} />
              
            </button>
          </div>
        </div>

        <div className="content">
          <div className="posts-list">
            {filteredPosts.map(post => (
              <div key={post.id} className="post-card">
                <div className="post-header">
                  <div className="post-author">
                    <div className="author-avatar">{post.author.charAt(0)}</div>
                    <div className="author-info">
                      <span className="author-name">{post.author}</span>
                      <span className="post-time">{formatDate(post.createdAt)}</span>
                    </div>
                  </div>
                  <button className="more-button">
                    <MoreHorizontal size={16} />
                  </button>
                </div>
                
                <div className="post-content">
                  <h4 className="post-title">{post.title}</h4>
                  <p className="post-text">{post.content}</p>
                  {post.tags && (
                    <div className="post-tags">
                      {post.tags.map(tag => (
                        <span key={tag} className="tag">#{tag}</span>
                      ))}
                    </div>
                  )}
                </div>
                
                <div className="post-actions">
                  <button 
                    className={`action-button ${post.isLiked ? 'liked' : ''}`}
                    onClick={() => handleLike(post.id)}
                  >
                    <ThumbsUp size={16} />
                    <span>{post.likes}</span>
                  </button>
                  <button className="action-button">
                    <MessageCircle size={16} />
                    <span>{post.comments}</span>
                  </button>
                  <button className="action-button">
                    <Eye size={16} />
                    <span>{post.views}</span>
                  </button>
                  <button 
                    className={`action-button ${post.isBookmarked ? 'bookmarked' : ''}`}
                    onClick={() => handleBookmark(post.id)}
                  >
                    <Bookmark size={16} />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  //    
  return (
    <div className="board-container">
      <div className="board-header">
        <div className="header-content">
          <h1></h1>
          <div className="header-actions">
            <button className="search-toggle">
              <Search size={20} />
            </button>
          </div>
        </div>
      </div>

      <div className="content">
        <div className="boards-grid">
          {boards.map(board => (
            <div key={board.id} className="board-card" onClick={() => handleBoardClick(board)}>
              <div className="board-icon">{board.icon}</div>
              <div className="board-info">
                <h3 className="board-name">{board.name}</h3>
                <p className="board-description">{board.description}</p>
                <div className="board-stats">
                  <span> {board.posts.toLocaleString()}</span>
                  <span> {board.members.toLocaleString()}</span>
                </div>
              </div>
              {board.isClub && (
                <div className="board-badge">
                  <Users size={14} />
                </div>
              )}
            </div>
          ))}
        </div>

        <div className="popular-posts">
          <h3> </h3>
          <div className="posts-preview">
            {posts.slice(0, 3).map(post => (
              <div key={post.id} className="post-preview">
                <h4>{post.title}</h4>
                <div className="post-preview-stats">
                  <span>{post.board}</span>
                  <span> {post.likes}</span>
                  <span> {post.comments}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Board;