import React, { useState } from 'react';
import { Heart, Bookmark, MessageSquare, MessageCircle, User, Settings, Bell, HelpCircle, LogOut, Trophy, Star, Calendar, BookOpen, Users, ShoppingBag, MapPin, Phone, Mail, Edit, Camera, Upload, ArrowLeft } from 'lucide-react';
import PostDetail from './PostDetail';
import './MyPage.css';

const MyPage = () => {
  const [activeTab, setActiveTab] = useState('liked');
  const [showSettings, setShowSettings] = useState(false);
  const [showProfileEdit, setShowProfileEdit] = useState(false);
  const [profileImage, setProfileImage] = useState('https://via.placeholder.com/80x80?text=');
  const [currentView, setCurrentView] = useState('main'); // main, postDetail, settings, accountSettings, notificationSettings, helpCenter
  const [selectedPost, setSelectedPost] = useState(null);
  const [notificationSettings, setNotificationSettings] = useState({
    pushNotifications: true,
    emailNotifications: false,
    commentNotifications: true,
    likeNotifications: true,
    followNotifications: true,
    marketplaceNotifications: false
  });
  const [accountSettings, setAccountSettings] = useState({
    name: '',
    email: 'student@university.ac.jp',
    phone: '010-1234-5678',
    major: '',
    year: '3',
    privacy: 'public' // public, friends, private
  });

  const userInfo = {
    name: '',
    major: '',
    year: '3',
    gpa: '3.85',
    credits: '98',
    joinDate: '2022 3',
    circles: [
      { name: 'CODE', role: '', joinDate: '2022 3', status: '' },
      { name: '', role: '', joinDate: '2022 9', status: '' },
      { name: '', role: '', joinDate: '2023 3', status: '' }
    ]
  };

  const likedPosts = [
    {
      id: 1,
      title: '  !',
      category: '',
      timeAgo: '2 ',
      likes: 23,
      comments: 8
    },
    {
      id: 2,
      title: '   ?',
      category: '',
      timeAgo: '1 ',
      likes: 45,
      comments: 12
    },
    {
      id: 3,
      title: '  !',
      category: '',
      timeAgo: '3 ',
      likes: 67,
      comments: 15
    }
  ];

  const savedItems = [
    {
      id: 1,
      title: 'iPhone 13 Pro 128GB',
      price: '850,000',
      image: 'https://images.unsplash.com/photo-1592750475338-74b7b21085ab?w=100&h=100&fit=crop',
      timeAgo: '1 '
    },
    {
      id: 2,
      title: '  ()',
      price: '25,000',
      image: 'https://images.unsplash.com/photo-1544716278-ca5e3f4abd8c?w=100&h=100&fit=crop',
      timeAgo: '2 '
    }
  ];

  const myPosts = [
    {
      id: 1,
      title: '   ',
      category: '',
      timeAgo: '5 ',
      likes: 34,
      comments: 7,
      views: 234
    },
    {
      id: 2,
      title: '  ',
      category: '',
      timeAgo: '1 ',
      likes: 12,
      comments: 3,
      views: 156
    }
  ];

  const achievements = [
    { icon: '', title: ' ', description: 'GPA 3.5  ' },
    { icon: '', title: ' ', description: ' 100 ' },
    { icon: '', title: ' ', description: ' 50 ' },
    { icon: '', title: ' ', description: ' 10 ' }
  ];

  const menuItems = [
    { icon: Bell, title: ' ', subtitle: ' ,  ' },
    { icon: Settings, title: ' ', subtitle: ',  ' },
    { icon: HelpCircle, title: '', subtitle: ', FAQ' },
    { icon: LogOut, title: '', subtitle: ' ' }
  ];

  const handleProfileImageChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      setProfileImage(reader.result);
    };
    reader.readAsDataURL(file);
  };

  const handlePostClick = (post) => {
    //   PostDetail  
    const postDetailData = {
      id: post.id,
      content: post.title,
      author: '',
      time: post.timeAgo,
      likes: post.likes || 0,
      comments: post.comments || 0,
      shares: 0,
      category: post.category || '',
      images: []
    };
    setSelectedPost(postDetailData);
    setCurrentView('postDetail');
  };

  const handleBackToMain = () => {
    setCurrentView('main');
    setSelectedPost(null);
  };

  const handleSettingsClick = (settingType) => {
    if (settingType === '') {
      if (window.confirm(' ?')) {
        alert('.');
        //     
      }
    } else if (settingType === ' ') {
      setCurrentView('accountSettings');
    } else if (settingType === ' ') {
      setCurrentView('notificationSettings');
    } else if (settingType === '') {
      setCurrentView('helpCenter');
    }
  };

  const handleNotificationChange = (key, value) => {
    setNotificationSettings(prev => ({
      ...prev,
      [key]: value
    }));
  };

  const handleAccountSettingsChange = (key, value) => {
    setAccountSettings(prev => ({
      ...prev,
      [key]: value
    }));
  };

  const handleSaveAccountSettings = () => {
    //  
    if (!accountSettings.name.trim()) {
      alert(' .');
      return;
    }
    
    //   
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(accountSettings.email)) {
      alert('   .');
      return;
    }
    
    //    (  )
    const phoneRegex = /^[0-9-]+$/;
    if (!phoneRegex.test(accountSettings.phone)) {
      alert('   . (  )');
      return;
    }
    
    //   (, ,  )
    const nameRegex = /^[-a-zA-Z0-9\s]+$/;
    if (!nameRegex.test(accountSettings.name)) {
      alert(' , ,   .');
      return;
    }
    
    //  
    if (!accountSettings.major) {
      alert(' .');
      return;
    }
    
    //    (  )
    // setUserProfile(prev => ({
    //   ...prev,
    //   name: accountSettings.name,
    //   email: accountSettings.email,
    //   phone: accountSettings.phone,
    //   major: accountSettings.major,
    //   year: accountSettings.year
    // }));
    
    alert('  .');
    setCurrentView('main');
  };

  const handleUpdatePost = (postId, updatedPost) => {
    setSelectedPost(updatedPost);
  };

  //   
  if (currentView === 'postDetail' && selectedPost) {
    return (
      <PostDetail 
        post={selectedPost}
        onBack={handleBackToMain}
        onUpdatePost={handleUpdatePost}
      />
    );
  }

  //   
  if (currentView === 'accountSettings') {
    return (
      <div className="mypage">
        <div className="settings-header">
          <button className="back-button" onClick={handleBackToMain}>
            <ArrowLeft size={20} />
            <span></span>
          </button>
          <h1> </h1>
        </div>

        <div className="settings-content">
          <div className="settings-section">
            <h3> </h3>
            <div className="settings-form">
              <div className="form-group">
                <label></label>
                <input 
                  type="text" 
                  value={accountSettings.name}
                  onChange={(e) => handleAccountSettingsChange('name', e.target.value)}
                  className="form-input"
                />
              </div>
              <div className="form-group">
                <label></label>
                <input 
                  type="email" 
                  value={accountSettings.email}
                  onChange={(e) => handleAccountSettingsChange('email', e.target.value)}
                  className="form-input"
                />
              </div>
              <div className="form-group">
                <label></label>
                <input 
                  type="tel" 
                  value={accountSettings.phone}
                  onChange={(e) => handleAccountSettingsChange('phone', e.target.value)}
                  className="form-input"
                />
              </div>
              <div className="form-group">
                <label></label>
                <select 
                  value={accountSettings.major}
                  onChange={(e) => handleAccountSettingsChange('major', e.target.value)}
                  className="form-select"
                >
                  <option value=""> </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div className="form-group">
                <label></label>
                <select 
                  value={accountSettings.year}
                  onChange={(e) => handleAccountSettingsChange('year', e.target.value)}
                  className="form-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                  <option value=""></option>
                </select>
              </div>
            </div>
          </div>

          <div className="settings-section">
            <h3></h3>
            <div className="privacy-options">
              <label className="radio-option">
                <input 
                  type="radio" 
                  name="privacy" 
                  value="public"
                  checked={accountSettings.privacy === 'public'}
                  onChange={(e) => handleAccountSettingsChange('privacy', e.target.value)}
                />
                <span> </span>
                <small>      </small>
              </label>
              <label className="radio-option">
                <input 
                  type="radio" 
                  name="privacy" 
                  value="friends"
                  checked={accountSettings.privacy === 'friends'}
                  onChange={(e) => handleAccountSettingsChange('privacy', e.target.value)}
                />
                <span></span>
                <small>      </small>
              </label>
              <label className="radio-option">
                <input 
                  type="radio" 
                  name="privacy" 
                  value="private"
                  checked={accountSettings.privacy === 'private'}
                  onChange={(e) => handleAccountSettingsChange('privacy', e.target.value)}
                />
                <span></span>
                <small>     </small>
              </label>
            </div>
          </div>

          <div className="settings-actions">
            <button className="save-button" onClick={handleSaveAccountSettings}>
              
            </button>
          </div>
        </div>
      </div>
    );
  }

  //   
  if (currentView === 'notificationSettings') {
    return (
      <div className="mypage">
        <div className="settings-header">
          <button className="back-button" onClick={handleBackToMain}>
            <ArrowLeft size={20} />
            <span></span>
          </button>
          <h1> </h1>
        </div>

        <div className="settings-content">
          <div className="settings-section">
            <h3> </h3>
            <div className="notification-options">
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>  </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.pushNotifications}
                    onChange={(e) => handleNotificationChange('pushNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>  </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.emailNotifications}
                    onChange={(e) => handleNotificationChange('emailNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
            </div>
          </div>

          <div className="settings-section">
            <h3> </h3>
            <div className="notification-options">
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>    </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.commentNotifications}
                    onChange={(e) => handleNotificationChange('commentNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>    </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.likeNotifications}
                    onChange={(e) => handleNotificationChange('likeNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>   </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.followNotifications}
                    onChange={(e) => handleNotificationChange('followNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
              <div className="notification-item">
                <div className="notification-info">
                  <span> </span>
                  <small>   </small>
                </div>
                <label className="toggle-switch">
                  <input 
                    type="checkbox"
                    checked={notificationSettings.marketplaceNotifications}
                    onChange={(e) => handleNotificationChange('marketplaceNotifications', e.target.checked)}
                  />
                  <span className="slider"></span>
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  //  
  if (currentView === 'helpCenter') {
    return (
      <div className="mypage">
        <div className="settings-header">
          <button className="back-button" onClick={handleBackToMain}>
            <ArrowLeft size={20} />
            <span></span>
          </button>
          <h1></h1>
        </div>

        <div className="settings-content">
          <div className="help-section">
            <h3>  </h3>
            <div className="faq-list">
              <div className="faq-item">
                <div className="faq-question">
                  <HelpCircle size={16} />
                  <span>  </span>
                </div>
                <div className="faq-answer">
                   &gt;       .
                </div>
              </div>
              <div className="faq-item">
                <div className="faq-question">
                  <HelpCircle size={16} />
                  <span> </span>
                </div>
                <div className="faq-answer">
                    ' ' .
                </div>
              </div>
              <div className="faq-item">
                <div className="faq-question">
                  <HelpCircle size={16} />
                  <span>   </span>
                </div>
                <div className="faq-answer">
                      '' .
                </div>
              </div>
            </div>
          </div>

          <div className="help-section">
            <h3></h3>
            <div className="contact-options">
              <div className="contact-item">
                <Mail size={20} />
                <div className="contact-info">
                  <span> </span>
                  <small>support@university-app.com</small>
                </div>
              </div>
              <div className="contact-item">
                <Phone size={20} />
                <div className="contact-info">
                  <span> </span>
                  <small>02-1234-5678 ( 9-18)</small>
                </div>
              </div>
            </div>
          </div>

          <div className="help-section">
            <h3> </h3>
            <div className="app-info">
              <div className="info-item">
                <span></span>
                <span>1.2.3</span>
              </div>
              <div className="info-item">
                <span> </span>
                <span>2024 3 15</span>
              </div>
              <div className="info-item">
                <span></span>
                <span>University App Team</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="mypage">
      <div className="profile-section">
        <div className="profile-header">
          <div className="profile-left">
            <div className="avatar-container">
              <img src={profileImage} alt="" className="profile-avatar" />
              <button 
                className="change-avatar-btn"
                onClick={() => document.getElementById('profile-image-input').click()}
              >
                <Camera size={16} />
              </button>
              <input
                id="profile-image-input"
                type="file"
                accept="image/*"
                onChange={handleProfileImageChange}
                style={{ display: 'none' }}
              />
            </div>
            <div className="profile-info">
              <h2 className="profile-name">{userInfo.name}</h2>
              <p className="profile-details">{userInfo.major}  {userInfo.year}</p>
              <p className="profile-stats">GPA: {userInfo.gpa}  : {userInfo.credits}</p>
            </div>
          </div>
          <button 
            className="edit-profile-btn"
            onClick={() => setShowProfileEdit(true)}
          >
            <Edit size={16} />
            <span></span>
          </button>
        </div>
      </div>

      <div className="achievements-section">
        <h3> </h3>
        <div className="achievements-grid">
          {achievements.map((achievement, index) => (
            <div key={index} className="achievement-card">
              <div className="achievement-icon">{achievement.icon}</div>
              <div className="achievement-info">
                <div className="achievement-title">{achievement.title}</div>
                <div className="achievement-description">{achievement.description}</div>
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="activity-section">
        <div className="activity-tabs">
          <button 
            className={`activity-tab ${activeTab === 'liked' ? 'active' : ''}`}
            onClick={() => setActiveTab('liked')}
          >
            <Heart size={16} />
            <span></span>
          </button>
          <button 
            className={`activity-tab ${activeTab === 'saved' ? 'active' : ''}`}
            onClick={() => setActiveTab('saved')}
          >
            <Bookmark size={16} />
            <span></span>
          </button>
          <button 
            className={`activity-tab ${activeTab === 'myposts' ? 'active' : ''}`}
            onClick={() => setActiveTab('myposts')}
          >
            <MessageSquare size={16} />
            <span> </span>
          </button>
          <button 
            className={`activity-tab ${activeTab === 'circles' ? 'active' : ''}`}
            onClick={() => setActiveTab('circles')}
          >
            <Users size={16} />
            <span> </span>
          </button>
        </div>

        <div className="activity-content">
          {activeTab === 'liked' && (
            <div className="liked-posts">
              {likedPosts.map(post => (
                <div 
                  key={post.id} 
                  className="activity-item clickable"
                  onClick={() => handlePostClick(post)}
                >
                  <div className="item-content">
                    <div className="item-title">{post.title}</div>
                    <div className="item-meta">
                      <span className="item-category">{post.category}</span>
                      <span></span>
                      <span className="item-time">{post.timeAgo}</span>
                    </div>
                  </div>
                  <div className="item-stats">
                    <div className="stat">
                      <Heart size={12} />
                      <span>{post.likes}</span>
                    </div>
                    <div className="stat">
                      <MessageSquare size={12} />
                      <span>{post.comments}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}

          {activeTab === 'saved' && (
            <div className="saved-items">
              {savedItems.map(item => (
                <div 
                  key={item.id} 
                  className="saved-item clickable"
                  onClick={() => handlePostClick({
                    id: item.id,
                    title: item.title,
                    category: '',
                    timeAgo: item.timeAgo,
                    likes: 0,
                    comments: 0
                  })}
                >
                  <img src={item.image} alt={item.title} className="saved-item-image" />
                  <div className="saved-item-info">
                    <div className="saved-item-title">{item.title}</div>
                    <div className="saved-item-price">{item.price}</div>
                    <div className="saved-item-time">{item.timeAgo}</div>
                  </div>
                </div>
              ))}
            </div>
          )}

          {activeTab === 'myposts' && (
            <div className="my-posts">
              {myPosts.map(post => (
                <div 
                  key={post.id} 
                  className="activity-item clickable"
                  onClick={() => handlePostClick(post)}
                >
                  <div className="item-content">
                    <div className="item-title">{post.title}</div>
                    <div className="item-meta">
                      <span className="item-category">{post.category}</span>
                      <span></span>
                      <span className="item-time">{post.timeAgo}</span>
                    </div>
                  </div>
                  <div className="item-stats">
                    <div className="stat">
                      <Heart size={12} />
                      <span>{post.likes}</span>
                    </div>
                    <div className="stat">
                      <MessageSquare size={12} />
                      <span>{post.comments}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}

          {activeTab === 'circles' && (
            <div className="my-circles">
              {userInfo.circles.map((circle, index) => (
                <div key={index} className="circle-card">
                  <div className="circle-header">
                    <div className="circle-icon"></div>
                    <div className="circle-info">
                      <div className="circle-name">{circle.name}</div>
                      <div className="circle-role">{circle.role}</div>
                    </div>
                    <div className="circle-status">
                      <span className={`status-badge ${circle.status === '' ? 'active' : 'inactive'}`}>
                        {circle.status}
                      </span>
                    </div>
                  </div>
                  <div className="circle-details">
                    <div className="circle-join-date">
                      <Calendar size={12} />
                      <span>: {circle.joinDate}</span>
                    </div>
                  </div>
                  <div className="circle-actions">
                    <button className="circle-action-btn">
                      <MessageSquare size={14} />
                      <span></span>
                    </button>
                    <button className="circle-action-btn">
                      <Users size={14} />
                      <span></span>
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      <div className="menu-section">
        <h3></h3>
        <div className="menu-list">
          {menuItems.map((item, index) => (
            <div 
              key={index} 
              className="menu-item clickable"
              onClick={() => handleSettingsClick(item.title)}
            >
              <div className="menu-icon">
                <item.icon size={20} />
              </div>
              <div className="menu-content">
                <div className="menu-title">{item.title}</div>
                <div className="menu-subtitle">{item.subtitle}</div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/*    */}
      {showProfileEdit && (
        <div className="modal-overlay" onClick={() => setShowProfileEdit(false)}>
          <div className="profile-edit-modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3> </h3>
              <button 
                className="close-button" 
                onClick={() => setShowProfileEdit(false)}
              >
                
              </button>
            </div>
            
            <div className="modal-content">
              <div className="profile-image-section">
                <div className="profile-image-container">
                  <img src={profileImage} alt=" " className="profile-image-large" />
                  <button className="change-image-btn">
                    <Camera size={16} />
                    <span> </span>
                  </button>
                </div>
              </div>
              
              <div className="form-section">
                <label></label>
                <input 
                  type="text" 
                  value={accountSettings.name}
                  onChange={(e) => setAccountSettings(prev => ({...prev, name: e.target.value}))}
                  className="profile-input"
                />
              </div>
              
              <div className="form-section">
                <label></label>
                <input 
                  type="text" 
                  value={accountSettings.major}
                  onChange={(e) => setAccountSettings(prev => ({...prev, major: e.target.value}))}
                  className="profile-input"
                />
              </div>
              
              <div className="form-section">
                <label></label>
                <select 
                  value={accountSettings.year}
                  onChange={(e) => setAccountSettings(prev => ({...prev, year: e.target.value}))}
                  className="profile-select"
                >
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div className="form-section">
                <label></label>
                <input 
                  type="email" 
                  value={accountSettings.email}
                  onChange={(e) => setAccountSettings(prev => ({...prev, email: e.target.value}))}
                  className="profile-input"
                />
              </div>
              
              <div className="form-section">
                <label></label>
                <input 
                  type="tel" 
                  value={accountSettings.phone}
                  onChange={(e) => setAccountSettings(prev => ({...prev, phone: e.target.value}))}
                  className="profile-input"
                />
              </div>
              
              <div className="modal-actions">
                <button 
                  className="cancel-btn"
                  onClick={() => setShowProfileEdit(false)}
                >
                  
                </button>
                <button 
                  className="save-btn"
                  onClick={() => {
                    alert(' !');
                    setShowProfileEdit(false);
                  }}
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MyPage;
