import React, { useState } from 'react';
import { ArrowLeft, Camera, MapPin, Hash, Users, X } from 'lucide-react';
import './CreatePost.css';

const CreatePost = ({ onBack, onCreatePost }) => {
  const [content, setContent] = useState('');
  const [category, setCategory] = useState('');
  const [board, setBoard] = useState('');
  const [isAnonymous, setIsAnonymous] = useState(false);
  const [images, setImages] = useState([]);
  const [circles, setCircles] = useState([]);

  const categories = ['', '', '', '', '', ''];
  const boards = ['', '', '', '', '', ''];
  const availableCircles = ['CODE', '', '', '', ''];

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!content.trim()) {
      alert(' .');
      return;
    }

    const newPost = {
      id: Date.now(),
      author: isAnonymous ? '' : '',
      isAnonymous,
      time: ' ',
      content: content.trim(),
      likes: 0,
      comments: 0,
      shares: 0,
      liked: false,
      saved: false,
      category,
      board,
      images: images,
      circles: circles
    };

    onCreatePost(newPost);
    onBack();
  };

  const handleImageAdd = () => {
    //    ( 5)
    if (images.length >= 5) {
      alert('  5   .');
      return;
    }
    
    //       
    const imageUrl = `https://via.placeholder.com/400x300?text=++${images.length + 1}`;
    setImages([...images, imageUrl]);
  };

  const handleImageRemove = (index) => {
    setImages(images.filter((_, i) => i !== index));
  };

  const handleCircleToggle = (circle) => {
    if (circles.includes(circle)) {
      setCircles(circles.filter(c => c !== circle));
    } else {
      setCircles([...circles, circle]);
    }
  };

  return (
    <div className="create-post">
      <div className="create-post-header">
        <button className="back-button" onClick={onBack}>
          <ArrowLeft size={24} />
        </button>
        <h1> </h1>
        <button 
          className="submit-button" 
          onClick={handleSubmit}
          disabled={!content.trim()}
        >
          
        </button>
      </div>

      <div className="create-post-content">
        <form onSubmit={handleSubmit}>
          {/*   */}
          <div className="form-section">
            <label className="form-label"></label>
            <div className="category-grid">
              {categories.map((cat) => (
                <button
                  key={cat}
                  type="button"
                  className={`category-chip ${category === cat ? 'active' : ''}`}
                  onClick={() => setCategory(cat)}
                >
                  {cat}
                </button>
              ))}
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <label className="form-label"></label>
            <select 
              value={board} 
              onChange={(e) => setBoard(e.target.value)}
              className="board-select"
            >
              {boards.map((b) => (
                <option key={b} value={b}>{b}</option>
              ))}
            </select>
          </div>

          {/*   */}
          <div className="form-section">
            <label className="checkbox-label">
              <input
                type="checkbox"
                checked={isAnonymous}
                onChange={(e) => setIsAnonymous(e.target.checked)}
              />
              <span className="checkbox-text"> </span>
            </label>
          </div>

          {/*   */}
          <div className="form-section">
            <label className="form-label"></label>
            <textarea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="   ?"
              className="content-textarea"
              rows={8}
            />
            <div className="character-count">
              {content.length}/1000
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <label className="form-label"></label>
            <div className="image-section">
              <button
                type="button"
                className={`add-image-button ${images.length >= 5 ? 'disabled' : ''}`}
                onClick={handleImageAdd}
                disabled={images.length >= 5}
              >
                <Camera size={20} />
                  ({images.length}/5)
              </button>
              
              {images.length > 0 && (
                <div className="image-preview-grid">
                  {images.map((image, index) => (
                    <div key={index} className="image-preview">
                      <img src={image} alt={` ${index + 1}`} />
                      <button
                        type="button"
                        className="remove-image"
                        onClick={() => handleImageRemove(index)}
                      >
                        <X size={16} />
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/*   */}
          <div className="form-section">
            <label className="form-label">
              <Users size={16} />
               
            </label>
            <div className="circles-grid">
              {availableCircles.map((circle) => (
                <button
                  key={circle}
                  type="button"
                  className={`circle-chip ${circles.includes(circle) ? 'active' : ''}`}
                  onClick={() => handleCircleToggle(circle)}
                >
                  {circle}
                </button>
              ))}
            </div>
          </div>

          {/*    */}
          <div className="form-section">
            <div className="additional-features">
              <button type="button" className="feature-button">
                <MapPin size={18} />
                 
              </button>
              <button type="button" className="feature-button">
                <Hash size={18} />
                
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreatePost;
