import React, { useState } from 'react';
import { Search, Filter, Plus, Heart, MapPin, Eye, MessageCircle, Clock, X, User } from 'lucide-react';
import CreateMarketplaceItem from './CreateMarketplaceItem';
import SellerProfile from './SellerProfile';
import './Marketplace.css';

const Marketplace = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [sortBy, setSortBy] = useState('latest');
  const [showSortMenu, setShowSortMenu] = useState(false);
  const [likedItems, setLikedItems] = useState(new Set());
  const [currentView, setCurrentView] = useState('main'); // 'main', 'wishlist', 'create', 'sellerProfile'
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [selectedSeller, setSelectedSeller] = useState(null);
  const [showProductDetail, setShowProductDetail] = useState(false);
  const [items, setItems] = useState([
    {
      id: 1,
      title: 'iPhone 13 Pro 128GB',
      price: 850000,
      image: 'https://images.unsplash.com/photo-1592750475338-74b7b21085ab?w=300&h=300&fit=crop',
      category: 'electronics',
      location: '',
      time: '2 ',
      likes: 15,
      comments: 3,
      views: 127,
      condition: '',
      seller: '',
      department: '',
      description: '  .     !',
      status: 'available'
    },
    {
      id: 2,
      title: '  13 M1',
      price: 1200000,
      image: 'https://images.unsplash.com/photo-1541807084-5c52b6b3adef?w=300&h=300&fit=crop',
      category: 'electronics',
      location: '',
      time: '5 ',
      likes: 23,
      comments: 5,
      views: 189,
      condition: '',
      seller: '',
      department: '',
      description: '  .  .',
      status: 'available'
    },
    {
      id: 3,
      title: ' ',
      price: 15000,
      image: 'https://images.unsplash.com/photo-1544716278-ca5e3f4abd8c?w=300&h=300&fit=crop',
      category: 'books',
      location: '',
      time: '1 ',
      likes: 8,
      comments: 2,
      views: 67,
      condition: '',
      seller: '',
      department: '',
      description: '   .   .',
      status: 'available'
    }
  ]);

  const categories = [
    { id: 'all', name: '', color: '#667eea' },
    { id: 'electronics', name: '', color: '#4facfe' },
    { id: 'books', name: '/', color: '#43e97b' },
    { id: 'clothing', name: '', color: '#fa709a' },
    { id: 'furniture', name: '/', color: '#fd79a8' },
    { id: 'sports', name: '', color: '#fdcb6e' },
    { id: 'beauty', name: '', color: '#e17055' },
    { id: 'etc', name: '', color: '#a29bfe' }
  ];

  const products = [
    {
      id: 1,
      title: 'iPhone 13 Pro 128GB',
      price: 850000,
      image: 'https://images.unsplash.com/photo-1592750475338-74b7b21085ab?w=300&h=300&fit=crop',
      category: 'electronics',
      location: '',
      timeAgo: '2 ',
      likes: 15,
      comments: 3,
      views: 127,
      condition: '',
      seller: '',
      description: '  .     !'
    },
    {
      id: 2,
      title: '  ()',
      price: 25000,
      image: 'https://images.unsplash.com/photo-1544716278-ca5e3f4abd8c?w=300&h=300&fit=crop',
      category: 'books',
      location: '',
      timeAgo: '5 ',
      likes: 8,
      comments: 1,
      views: 89,
      condition: '',
      seller: '20',
      description: '       .'
    },
    {
      id: 3,
      title: '  270mm',
      price: 120000,
      image: 'https://images.unsplash.com/photo-1549298916-b41d501d3772?w=300&h=300&fit=crop',
      category: 'clothing',
      location: '',
      timeAgo: '1 ',
      likes: 23,
      comments: 7,
      views: 234,
      condition: '',
      seller: '',
      description: '     .   !'
    },
    {
      id: 4,
      title: '  +  ',
      price: 180000,
      image: 'https://images.unsplash.com/photo-1586023492125-27b2c045efd7?w=300&h=300&fit=crop',
      category: 'furniture',
      location: '',
      timeAgo: '3 ',
      likes: 12,
      comments: 4,
      views: 156,
      condition: '',
      seller: '',
      description: ' .  .'
    },
    {
      id: 5,
      title: '  13 M1',
      price: 1200000,
      image: 'https://images.unsplash.com/photo-1517336714731-489689fd1ca8?w=300&h=300&fit=crop',
      category: 'electronics',
      location: '',
      timeAgo: '6 ',
      likes: 45,
      comments: 12,
      views: 567,
      condition: '',
      seller: '',
      description: '1  AppleCare+ . ,   '
    },
    {
      id: 6,
      title: '  + ',
      price: 35000,
      image: 'https://images.unsplash.com/photo-1582719471384-894fbb16e074?w=300&h=300&fit=crop',
      category: 'clothing',
      location: '',
      timeAgo: '12 ',
      likes: 6,
      comments: 2,
      views: 78,
      condition: '',
      seller: '22',
      description: '   .  .'
    }
  ];

  const filteredProducts = items.filter(product => {
    const matchesCategory = selectedCategory === 'all' || product.category === selectedCategory;
    const matchesSearch = product.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         product.description.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesCategory && matchesSearch;
  }).sort((a, b) => {
    switch (sortBy) {
      case 'price_low':
        return a.price - b.price;
      case 'price_high':
        return b.price - a.price;
      case 'popular':
        return b.likes - a.likes;
      case 'latest':
      default:
        return new Date(b.timeAgo) - new Date(a.timeAgo);
    }
  });

  const formatPrice = (price) => {
    return price.toLocaleString() + '';
  };

  const getCategoryColor = (categoryId) => {
    return categories.find(cat => cat.id === categoryId)?.color || '#667eea';
  };

  const handleCreateItem = (newItem) => {
    setItems(prev => [newItem, ...prev]);
  };

  const handleBackToMain = () => {
    setCurrentView('main');
  };

  const handleAddItem = () => {
    setCurrentView('create');
  };

  const toggleLike = (itemId) => {
    setLikedItems(prev => {
      const newLiked = new Set(prev);
      if (newLiked.has(itemId)) {
        newLiked.delete(itemId);
      } else {
        newLiked.add(itemId);
      }
      return newLiked;
    });
  };

  const handleProductClick = (product) => {
    setSelectedProduct(product);
    setShowProductDetail(true);
  };

  const closeProductDetail = () => {
    setShowProductDetail(false);
    setSelectedProduct(null);
  };

  const handleSellerClick = (seller) => {
    setSelectedSeller({
      name: seller,
      department: items.find(item => item.seller === seller)?.department || ' '
    });
    setCurrentView('sellerProfile');
  };

  if (currentView === 'create') {
    return (
      <CreateMarketplaceItem 
        onBack={handleBackToMain}
        onCreateItem={handleCreateItem}
      />
    );
  }

  if (currentView === 'sellerProfile') {
    return (
      <SellerProfile 
        seller={selectedSeller}
        onBack={handleBackToMain}
      />
    );
  }

  return (
    <div className="marketplace">
      <header className="marketplace-header">
        <h1></h1>
        <div className="header-subtitle">   </div>
        
        {/*   */}
        <div className="marketplace-tabs">
          <button 
            className={`tab-button ${currentView === 'main' ? 'active' : ''}`}
            onClick={() => setCurrentView('main')}
          >
            
          </button>
          <button 
            className={`tab-button ${currentView === 'wishlist' ? 'active' : ''}`}
            onClick={() => setCurrentView('wishlist')}
          >
            <Heart size={16} />
             ({likedItems.size})
          </button>
        </div>
      </header>

      <div className="search-section">
        <div className="search-bar">
          <Search size={20} />
          <input
            type="text"
            placeholder=",  ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <div className="filter-container">
          <button 
            className="filter-button"
            onClick={() => setShowSortMenu(!showSortMenu)}
          >
            <Filter size={20} />
          </button>
          {showSortMenu && (
            <div className="sort-menu">
              <button 
                className={`sort-option ${sortBy === 'latest' ? 'active' : ''}`}
                onClick={() => {setSortBy('latest'); setShowSortMenu(false);}}
              >
                
              </button>
              <button 
                className={`sort-option ${sortBy === 'price_low' ? 'active' : ''}`}
                onClick={() => {setSortBy('price_low'); setShowSortMenu(false);}}
              >
                 
              </button>
              <button 
                className={`sort-option ${sortBy === 'price_high' ? 'active' : ''}`}
                onClick={() => {setSortBy('price_high'); setShowSortMenu(false);}}
              >
                 
              </button>
              <button 
                className={`sort-option ${sortBy === 'popular' ? 'active' : ''}`}
                onClick={() => {setSortBy('popular'); setShowSortMenu(false);}}
              >
                
              </button>
            </div>
          )}
        </div>
      </div>

      <div className="category-tabs">
        {categories.map(category => (
          <button
            key={category.id}
            className={`category-tab ${selectedCategory === category.id ? 'active' : ''}`}
            onClick={() => setSelectedCategory(category.id)}
            style={selectedCategory === category.id ? { 
              backgroundColor: category.color,
              color: 'white'
            } : {}}
          >
            {category.name}
          </button>
        ))}
      </div>

      <div className="marketplace-content">
        {currentView === 'main' ? (
          <>
            <div className="content-header">
              <span className="result-count">{filteredProducts.length} </span>
            </div>

            <div className="products-container grid">
              {filteredProducts.map(product => (
            <div key={product.id} className="product-card" onClick={() => handleProductClick(product)}>
              <div className="product-header">
                <div className="product-badge" style={{ backgroundColor: getCategoryColor(product.category) }}>
                  {categories.find(cat => cat.id === product.category)?.name}
                </div>
                <div className="seller-department">{product.department}</div>
              </div>

              <div className="product-image-container">
                <div className="product-image">
                  <img src={product.image} alt={product.title} />
                  <div className="price-overlay">
                    {formatPrice(product.price)}
                  </div>
                  <button 
                    className={`like-button ${likedItems.has(product.id) ? 'liked' : ''}`}
                    onClick={(e) => {
                      e.stopPropagation();
                      toggleLike(product.id);
                    }}
                  >
                    <Heart size={16} fill={likedItems.has(product.id) ? '#ff6b6b' : 'none'} />
                  </button>
                </div>
              </div>
              
              <div className="product-info">
                <div className="product-title">{product.title}</div>
                
                <div className="product-detail-row">
                  <div className="product-condition">{product.condition}</div>
                  <div className="product-spacing"></div>
                  <div 
                    className="seller-name clickable"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleSellerClick(product.seller);
                    }}
                  >
                    {product.seller}
                  </div>
                </div>
                
                <div className="product-meta">
                  <div className="product-location-time">
                    <div className="meta-item">
                      <MapPin size={12} />
                      <span>{product.location}</span>
                    </div>
                    <div className="meta-item">
                      <Clock size={12} />
                      <span>{product.time}</span>
                    </div>
                  </div>

                  <div className="product-stats">
                    <div className="stat-item">
                      <Heart size={12} />
                      <span>{product.likes}</span>
                    </div>
                    <div className="stat-item">
                      <MessageCircle size={12} />
                      <span>{product.comments}</span>
                    </div>
                    <div className="stat-item">
                      <Eye size={12} />
                      <span>{product.views}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
              ))}
            </div>
          </>
        ) : currentView === 'wishlist' ? (
          <div className="wishlist-content">
            <div className="content-header">
              <span className="result-count"> {likedItems.size}</span>
            </div>
            
            {likedItems.size === 0 ? (
              <div className="empty-wishlist">
                <Heart size={48} color="#e1e8ed" />
                <h3> </h3>
                <p>    !</p>
                <button 
                  className="browse-button"
                  onClick={() => setCurrentView('main')}
                >
                   
                </button>
              </div>
            ) : (
              <div className="products-container grid">
                {items.filter(product => likedItems.has(product.id)).map(product => (
                  <div key={product.id} className="product-card" onClick={() => handleProductClick(product)}>
                    <div className="product-header">
                      <div className="product-badge" style={{ backgroundColor: getCategoryColor(product.category) }}>
                        {categories.find(cat => cat.id === product.category)?.name}
                      </div>
                      <div className="seller-department">{product.department}</div>
                    </div>

                    <div className="product-image-container">
                      <div className="product-image">
                        <img src={product.image} alt={product.title} />
                        <div className="price-overlay">
                          {formatPrice(product.price)}
                        </div>
                        <button 
                          className={`like-button ${likedItems.has(product.id) ? 'liked' : ''}`}
                          onClick={(e) => {
                            e.stopPropagation();
                            toggleLike(product.id);
                          }}
                        >
                          <Heart size={16} fill={likedItems.has(product.id) ? '#ff6b6b' : 'none'} />
                        </button>
                      </div>
                    </div>
                    
                    <div className="product-info">
                      <div className="product-title">{product.title}</div>
                      
                      <div className="product-detail-row">
                        <div className="product-condition">{product.condition}</div>
                        <div className="product-spacing"></div>
                        <div 
                    className="seller-name clickable"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleSellerClick(product.seller);
                    }}
                  >
                    {product.seller}
                  </div>
                      </div>
                      
                      <div className="product-meta">
                        <div className="product-location-time">
                          <div className="meta-item">
                            <MapPin size={12} />
                            <span>{product.location}</span>
                          </div>
                          <div className="meta-item">
                            <Clock size={12} />
                            <span>{product.time}</span>
                          </div>
                        </div>

                        <div className="product-stats">
                          <div className="stat-item">
                            <Heart size={12} />
                            <span>{product.likes}</span>
                          </div>
                          <div className="stat-item">
                            <MessageCircle size={12} />
                            <span>{product.comments}</span>
                          </div>
                          <div className="stat-item">
                            <Eye size={12} />
                            <span>{product.views}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ) : null}
      </div>

      <button 
        className="floating-add-button"
        onClick={handleAddItem}
      >
        <Plus size={24} />
      </button>

      {/*    */}
      {showProductDetail && selectedProduct && (
        <div className="modal-overlay" onClick={closeProductDetail}>
          <div className="product-detail-modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>{selectedProduct.title}</h3>
              <button className="close-button" onClick={closeProductDetail}>
                <X size={24} />
              </button>
            </div>
            
            <div className="modal-body">
              <div className="product-detail-image">
                <img src={selectedProduct.image} alt={selectedProduct.title} />
                <div className="price-overlay-large">
                  {formatPrice(selectedProduct.price)}
                </div>
              </div>
              
              <div className="product-detail-info">
                <div className="detail-row">
                  <div className="detail-label"> :</div>
                  <div className="detail-value">{selectedProduct.condition}</div>
                </div>
                
                <div className="detail-row">
                  <div className="detail-label">:</div>
                  <div className="detail-value">
                    <User size={16} />
                    {selectedProduct.seller} ({selectedProduct.department})
                  </div>
                </div>
                
                <div className="detail-row">
                  <div className="detail-label">:</div>
                  <div className="detail-value">
                    <MapPin size={16} />
                    {selectedProduct.location}
                  </div>
                </div>
                
                <div className="detail-row">
                  <div className="detail-label"> :</div>
                  <div className="detail-value">
                    <Clock size={16} />
                    {selectedProduct.time}
                  </div>
                </div>
                
                <div className="product-description">
                  <h4> </h4>
                  <p>{selectedProduct.description}</p>
                </div>
                
                <div className="product-stats-detail">
                  <div className="stat-item-detail">
                    <Heart size={16} />
                    <span> {selectedProduct.likes}</span>
                  </div>
                  <div className="stat-item-detail">
                    <MessageCircle size={16} />
                    <span> {selectedProduct.comments}</span>
                  </div>
                  <div className="stat-item-detail">
                    <Eye size={16} />
                    <span> {selectedProduct.views}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Marketplace;
